@import url(http://fonts.googleapis.com/css?family=Roboto+Slab|Open+Sans:400italic,700italic,400,700);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;700&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0px;
  padding: 0px;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}

body {
  background-color: #2F323A;
}

.navbar {
  display: flex;
  justify-content: space-between;
  background-color: #2F323A;
  color: #9C8266;
}

.navbar__left {
  flex: 0.3 1;
}

.navbar__left img {
  height: 104px;
  width: 300px;
  -webkit-transform: scale(2.5);
          transform: scale(2.5);
}

.navbar__right {
  display: flex;
  flex: 0.5 1;
  justify-content: space-around;
  font-size: 1.1em;
  align-items: center;
  text-transform: uppercase;
}

.navbar__right a {
  text-decoration: none;
}

.navbar__right a:visited {
  color: #9C8266;
}

@media only screen and (max-width: 600px) {
  .navbar {
    width: 100vw;
    justify-content: center;
  }
  .navbar .navbar__left img {
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
    width: auto;
  }
  .navbar__right {
    display: none;
  }
}

.home__banner {
  display: flex;
  justify-content: space-around;
  height: 600px;
  align-items: center;
  padding-bottom: 20px;
}

.home__banner-left {
  display: flex;
  flex: 0.4 1;
  flex-direction: column;
  color: #F2CB96;
  align-items: center;
  font-size: 2rem;
}

.home__banner-left span {
  text-align: center;
}

.home__banner-left div {
  margin-top: 20px;
  border-radius: 30px;
  color: #9C8266;
  background-color: #C4C4C4;
  justify-content: center;
  font-size: 1rem;
  padding: 0px 20px;
}

.home__banner-left div span {
  align-items: center;
  display: flex;
  height: 40px;
}

.home__banner-right {
  display: flex;
  flex: 0.4 1;
  justify-content: center;
}

.home__banner-right img {
  width: 100%;
}

@media only screen and (max-width: 600px) {
  .home__banner {
    flex-direction: column-reverse;
    height: 65vh;
  }
  .home__banner-right img {
    width: 90%;
  }
  .home__banner-left {
    flex: 0.2 1;
    padding: 0 30px;
    justify-content: center;
    align-items: center;
  }
  .home__banner-left span {
    font-size: 1.8rem;
  }
}

.homeSolution {
  background-color: #C4C4C4;
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  justify-content: center;
  align-items: center;
  padding: 0 10%;
}

.homeSolution h1 {
  color: #9C8266;
  font-size: 2rem;
  margin: 50px 0;
}

.homeSolution div span {
  margin-top: 20px;
  border-radius: 30px;
  color: #9C8266;
  background-color: #2F323A;
  justify-content: center;
  font-size: 2rem;
  padding: 0px 20px;
}

.homeSolution div:last-of-type {
  margin-bottom: 50px;
}

.homeSolution__first, .homeSolution__second {
  display: flex;
  width: 100%;
  margin-bottom: 50px;
}

.homeSolution__solution__image {
  display: flex;
  width: 20%;
  flex: 0.5 1;
  justify-content: center;
}

.homeSolution__solution__image img {
  object-fit: contain;
  width: 60%;
}

.homeSolution__solution__text {
  display: flex;
  flex-direction: column;
  flex: 0.5 1;
  padding: 50px;
  justify-content: center;
}

.homeSolution__solution__text h2 {
  text-align: center;
  font-size: 2rem;
}

.homeSolution__solution__text p {
  text-align: center;
  font-size: 1.3rem;
}

@media only screen and (max-width: 600px) {
  .homeSolution h1 {
    font-size: 2rem;
    margin: 2rem 0;
  }
  .homeSolution div {
    margin-bottom: 50px;
  }
  .homeSolution__block {
    flex-direction: column;
  }
  .homeSolution__block .homeSolution__solution__image {
    flex: 1 1;
    width: 100%;
  }
  .homeSolution__block .homeSolution__solution__image img {
    object-fit: contain;
    width: 100%;
  }
  .homeSolution__second {
    flex-direction: column-reverse;
  }
  .homeSolution__solution__text {
    padding: 0;
  }
}

.homeneed {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  justify-content: center;
  align-items: center;
  padding: 0 10%;
  padding-bottom: 40px;
}

.homeneed h1 {
  color: #9C8266;
  font-size: 2rem;
  margin: 50px 0;
  text-align: center;
}

.homeneed__block {
  display: flex;
}

.homeneed__image {
  display: flex;
  width: 20%;
  flex: 0.5 1;
  justify-content: center;
}

.homeneed__image img {
  object-fit: contain;
  width: 60%;
  border-radius: 250.5px;
}

.homeneed__content {
  display: flex;
  flex: 0.5 1;
  font-size: 1.3rem;
  padding: 50px;
  justify-content: center;
  color: #fff;
}

.homeneed__content p {
  text-align: center;
}

.homeneed__content p span {
  font-size: 2rem;
}

@media only screen and (max-width: 600px) {
  .homeneed h1 {
    font-size: 2rem;
    margin: 2rem 0;
  }
  .homeneed__block {
    flex-direction: column;
  }
  .homeneed__image {
    width: 100%;
  }
  .homeneed__content {
    padding: 30px 0px;
  }
  .homeneed__content p {
    font-size: 1.2rem;
  }
  #need_second {
    flex-direction: column-reverse;
  }
}

.homehorizontal {
  background-color: #C4C4C4;
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  justify-content: center;
  align-items: center;
  padding: 0 10%;
}

.homehorizontal h1 {
  color: #9C8266;
  font-size: 2rem;
  margin: 50px 0;
  text-align: center;
}

.homehorizontal__content {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.homehorizontal__content-first, .homehorizontal__content-second {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
}

.homehorizontal__content-item {
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
  align-items: center;
  width: 330px;
}

.homehorizontal__content-item img {
  object-fit: contain;
  width: 70%;
  margin-top: 30px;
}

.homehorizontal__content-item span {
  font-size: 1.3rem;
  font-family: "Montserrat", serif;
  padding: 5px;
  text-align: center;
}

@media only screen and (max-width: 600px) {
  .homehorizontal h1 {
    font-size: 2rem;
    margin: 2rem 0;
  }
  .homehorizontal__content {
    width: 100vw;
  }
  .homeSolution__solution__text h2 {
    font-size: 1.6rem;
  }
  .homeSolution__solution__text p {
    font-size: 1.3rem;
  }
}

.homecontactus {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 50px 0;
}

.homecontactus__left {
  display: flex;
  flex-direction: column;
  flex: 0.3 1;
  justify-content: center;
  padding: 0 100px;
}

.homecontactus__left img {
  height: 40px;
  margin-top: 20px;
}

.homecontactus__left h2 {
  color: #9C8266;
}

.homecontactus__left p {
  color: #fff;
  font-size: 1.2rem;
}

.homecontactus__left .homecontactus__left__image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.homecontactus__right {
  display: flex;
  flex: 0.5 1;
  flex-direction: column;
  align-items: center;
}

.homecontactus__right input {
  text-decoration: none;
  background-color: #C4C4C4;
  border: none;
  margin: 10px;
  font-size: 1.5rem;
  border-radius: 10px;
  padding: 5px 20px;
}

.homecontactus__right input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.39);
  font-size: 1rem;
  display: flex;
  justify-content: center;
}

.homecontactus__right input:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.39);
  font-size: 1rem;
  display: flex;
  justify-content: center;
}

.homecontactus__right input::placeholder {
  color: rgba(0, 0, 0, 0.39);
  font-size: 1rem;
  display: flex;
  justify-content: center;
}

.homecontactus__right input:focus {
  border: none;
  outline: none;
}

.homecontactus__right button {
  margin-top: 10px;
  width: 120px;
  border: none;
  height: 35px;
  border-radius: 10px;
}

.homecopyright {
  display: flex;
  background-color: #2F323A;
  width: 100%;
  justify-content: center;
  color: #fff;
  padding: 10px 0 px;
}

.homecopyright h6 {
  font-size: 1rem;
}

@media only screen and (max-width: 600px) {
  .homecontactus {
    flex-direction: column;
  }
  .homecontactus__left {
    padding: 0 10%;
  }
  .homecontactus__right input {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 768px) {
  .homecontactus__left {
    padding: 0 50px;
  }
  .homecontactus__left h2 {
    font-size: 2rem;
  }
}

.learnMore .homecontactus {
  background-color: #C4C4C4;
  padding-bottom: 20px;
  margin-bottom: 0px;
}

.learnMore .homecontactus .homecontactus__left__content p {
  color: #000;
}

.learnMore .homecontactus .homecontactus__right input {
  background-color: #2F323A;
}

.learnMore .homecontactus .homecontactus__right input::-webkit-input-placeholder {
  color: #959ba9;
}

.learnMore .homecontactus .homecontactus__right input:-ms-input-placeholder {
  color: #959ba9;
}

.learnMore .homecontactus .homecontactus__right input::placeholder {
  color: #959ba9;
}

.learnMore .homecontactus .homecontactus__right button {
  background-color: #2F323A;
  color: #fff;
}

.learnMore__datils {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #9C8266;
  margin: 0 10%;
}

.learnMore__datils h1 {
  text-decoration: underline;
}

.learnMore__details__block {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.learnMore__details__block #content {
  display: flex;
  flex-direction: column;
  flex: 0.4 1;
  align-items: center;
  text-align: center;
  padding: 50px 30px;
}

.learnMore__details__block #content h3 {
  font-size: 1.4rem;
}

.learnMore__details__block #content p {
  font-size: 1.2rem;
}

.learnMore__details__block #image {
  display: flex;
  flex: 0.5 1;
  justify-content: center;
}

.learnMore__details__block #image img {
  object-fit: contain;
  width: 300px;
}

@media only screen and (max-width: 600px) {
  .learnMore__datils #second {
    flex-direction: column-reverse !important;
  }
  .learnMore__details__block {
    flex-direction: column;
  }
}
